import LoadMore from '~/components/LoadMore'
import utils from '~/common/utils'

export default {
  components: {
    LoadMore
  },
  props: {
    entityType: {
      type: String,
      default: '',
      required: true
    },
    entityId: {
      type: Number,
      default: 0,
      required: true
    },
    commentsPage: {
      type: Object,
      default() {
        return {}
      }
    },
    showAd: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    user() {
      return this.$store.state.user.current
    },
    isLogin() {
      return this.$store.state.user.current != null
    }
  },
  methods: {
    append(data) {
      if (!data) return

      this.$refs.commentsLoadMore.unshiftResults(data)
    },
    reply(quote) {
      if (!this.isLogin) {
        utils.toSignin()
      }
      this.$emit('reply', quote)
    },
    cancelReply() {
      this.quote = null
    }
  }
}
